import axios from 'axios';

const url = 'api/restaurants/';

const axiosInstance = axios.create({
    baseURL: process.env.BASE_URL
});

class RestaurantService {

    static getRestaurants() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(restaurant => ({
                        ...restaurant,
                            name: restaurant.name
                        })
                    )
                )
            }
            catch(err) {
                reject(err);
            }
        })
    }

    static getCloseRestaurants(coord, time) {
        return new Promise(async (resolve, reject) => {
            try {
                console.log(`${url}${coord}/ ${time}`)
                const res = await axiosInstance.get(`${url}${coord}/ ${time}`);
                const data = res.data;
                resolve(
                    data.map(restaurant => ({
                            ...restaurant,
                            name: restaurant.name
                        })
                    )
                )
            }
            catch(err) {
                reject(err);
            }
        })
    }

    static insertRestaurant(data) {
        return axios.post(url, {
            name: data,
            address: '',
            number: '',
            Monday: '',
            Tuesday: '',
            Wednesday: '',
            Thursday: '',
            Friday: '',
            Saturday: '',
            Sunday: ''
        });
    }

    static deleteResturant(id) {
        return axios.delete(`${url}${id}`)
    }
}

export default RestaurantService;
